nt-size:2.2em;font-weight:900;color:#fff;-webkit-animation:spin 2s linear;animation:spin 2s linear}.replayBtn:hover{background:rgb(254, 202, 27);background:var(--yellow);outline:none}@keyframes spin{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}100%{-webkit-transform:rotate(270deg);transform:rotate(270deg)}}.rotate-div{width:100%;height:100%;background:#fe5350;background:var(--red);display:none}@media (orientation: portrait){.bg{z-index:999}.bg img,.bg .rotate-div{display:block}}.small-btn{text-shadow:1px 1px pink, -1px -1px maroon;font-size:22pt;width:1.5em;border-radius:.75em;background-color:#fe5350;background-color:var(--red);-o-box-shadow:0 .2em maroon;box-shadow:0 0.2em maroon;color:#fe5350;color:var(--red);background-color:#fe5350;background-color:var(--red);background-image:linear-gradient(left top, pink 3%, #fe5350 22%, maroon 99%);background-image:linear-gradient(left top, pink 3%, var(--red) 22%, maroon 99%);cursor:pointer;border-radius:50%;width:10px;display:flex;justify-content:center;margin:0 .5rem}.index-btn{text-align:center;font-weight:bold;padding:37px;background-color:rgb(254, 202, 27);background-color:var(--yellow);text-shadow:-1px -1px black, 1px 1px white;color:gray;border-radius:7px;box-shadow:0 0.2em grey;cursor:pointer;background:lightgrey;display:flex;justify-content:center}.small-btn:active,.index-btn:active{box-shadow:none;position:relative;top:.2em}

