nav{display:flex;justify-content:space-between;align-items:center}nav *{color:#fff}nav ul{display:flex;justify-content:flex-end;z-index:99}nav ul a,nav ul li{list-style:none;padding:.5em;font-size:1.2em;text-decoration:none;cursor:pointer;background:rgba(0,0,0,0.1)}nav .fas,nav li{font-size:2.6em}nav .btn{position:relative}nav .btn:link,nav .btnvisited{text-transform:uppercase;display:inline-block;transition:all .2s;text-align:center}nav .btn:hover{-webkit-transform:translateY(-3px);transform:translateY(-3px);box-shadow:0 10px 20px rgba(0,0,0,0.2)}nav .btn:active{-webkit-transform:translateY(-1px);transform:translateY(-1px);box-shadow:0 5px 10px rgba(0,0,0,0.2)}nav .btn::after{content:"";display:inline-block;height:100%;width:100%;position:absolute;top:0;left:0;z-index:-1;transition:all .4s}nav .btn .fa-play{margin-left:.17em}nav .btn:hover::after{-webkit-transform:scaleX(1.4) scaleY(1.6);transform:scaleX(1.4) scaleY(1.6);opacity:0}nav .btn .btn-animated{-webkit-animation:moveInBottom 5s ease-out;animation:moveInBottom 5s ease-out;-webkit-animation-fill-mode:backwards;animation-fill-mode:backwards}nav .btn .btn-white::after{background-color:rgba(0,0,0,0.1)}nav .tooltiptext{visibility:hidden;width:-webkit-max-content;width:-moz-max-content;width:max-content;background-color:#000;color:grey;text-align:center;border-radius:6px;padding:.5em;z-index:999 !important;position:absolute;right:0;top:calc(100% + 1em);font-size:16px}.tooltip:hover .tooltiptext{visibility:visible;z-index:999}@-webkit-keyframes moveInBottom{0%{opacity:0;-webkit-transform:translateY(30px);transform:translateY(30px)}100%{opacity:1;background:var(--red);-webkit-transform:translateY(0px);transform:translateY(0px)}}@keyframes moveInBottom{0%{opacity:0;-webkit-transform:translateY(30px);transform:translateY(30px)}100%{opacity:1;background:var(--red);-webkit-transform:translateY(0px);transform:translateY(0px)}}.active-link{background:var(--red)}.fullscreenBtn{font-size:2em;background:rgba(255,255,255,0.1