.Home{margin:0 auto;display:flex;flex-direction:column;align-items:center;justify-content:center;position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);width:900px}.Home *{font-size:37pt;text-transform:uppercase;color:gray}.Home ul{display:flex}.Home .menu{display:flex;flex-direction:column}.Home .menu a{text-decoration:none;margin-bottom:1.1em}.Home .small-btn-div{display:flex}

.remark{background:#000;border-radius:5px;-webkit-animation:larger 2s linear forwards;animation:larger 2s linear forwards}.remark:hover{color:var(--red)}.remark::before{content:'';display:block;position:absolute;top:100%;left:50%;-webkit-transform:translateX(-50%);transform:translateX(-50%);width:0;height:0;border-left:16px solid transparent;border-right:16px solid transparent;border-top:16px solid rgba(0,0,0,0.1)}@-webkit-keyframes larger{0%{color:grey;z-index:999;font-size:1.6em;padding:1.5em 5em}100%{transition:1.2s;margin:0 auto;font-size:12px;padding:1em;margin:0 auto}}@keyframes larger{0%{color:grey;z-index:999;font-size:1.6em;padding:1.5em 5em}100%{transition:1.2s;margin:0 auto;font-size:12px;padding:1em;margin:0 auto}}.flip-card{background-color:transparent;width:auto;height:100%}.flip-card-inner{position:relative;width:100%;height:100%;text-align:center;transition:-webkit-transform 0.6s;transition:transform 0.6s;transition:transform 0.6s, -webkit-transform 0.6s;-webkit-transform-style:preserve-3d;transform-style:preserve-3d;box-shadow:0 4px 8px 0 rgba(0,0,0,0.2)}.flip-card:hover .flip-card-inner{-webkit-transform:rotateY(180deg);transform:rotateY(180deg)}.flip-card-front,.flip-card-back{border-radius:15px;position:absolute;width:100%;height:100%;-webkit-backface-visibility:hidden;backface-visibility:hidden;display:flex;flex-direction:column;align-items:center;justify-content:center}.flip-card-front{background-color:lightgrey;color:grey}.flip-card-back{background-color:var(--red);color:white;-webkit-transform:rotateY(180deg);transform:rotateY(180deg)}.play-pannel{display:flex;flex-direction:column;position:absolute;top:50%;-webkit-transform:translateY(-50%);transform:translateY(-50%);color:grey;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}.play-pannel .top-div{align-items:center;display:flex;height:7em;margin:0 auto;justify-content:space-between;height:6em;width:92vh;min-width:720px}.play-zone{min-width:720px;margin:0 auto;width:92vh;position:relative}.pokesDiv{display:grid;grid-template-columns:repeat(4, 1fr);grid-gap:1em;gap:1em}.pokesDiv .pokeCard{width:100%;padding-top:100%;position:relative}.pokesDiv .pokeCard .flip-card{position:absolute;top:50%;left:50%;width:100%;height:100%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}.pokesDiv .pokeCard *{font-size:12px}.pokesDiv .pokeCard .name{position:absolute;bottom:1em}.img-wrapper{width:100%;padding-top:100%}.img-wrapper img{width:100%;height:100%;position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}.pagination{width:100%}.pagination button{background:var(--red);border:0;margin:.5em;font-size:1.2rem;color:#fff;border-radius:50px;padding:1em;outline:none;cursor:pointer;top:50%;-webkit-transform:translateY(-50%);transform:translateY(-50%);position:absolute}.pagination button:hover{background:var(--yellow);transition:all .2s}.pagination button:before{content:'';display:block;border:5px solid #fff;width:20px;height:20px;-webkit-transform:rotate(45deg);transform:rotate(45deg);border-top:0;border-right:0}.pagination button.next:before{-webkit-transform:rotate(230deg);transform:rotate(230deg)}.pagination .next{left:100%}.pagination .prev{left:-4.2em}.top-div-active{width:100% !important;height:100vh !important;background:#000}.top-div-active .teamDiv{justify-content:center;height:50%}.top-div-active .teamDiv *{color:var(--black) color： #fff}.teamDiv-img-active{width:12em !important;transition:all 1.2s}.winlose-div{-webkit-animation:showup 1.6s;animation:showup 1.6s;background:rgba(255,255,255,0.15);width:100%;height:100%;position:absolute;display:flex;flex-direction:column;align-items:center;justify-content:center}@-webkit-keyframes showup{from{opacity:0}to{opacity:1}}@keyframes showup{from{opacity:0}to{opacity:1}}.scroll-active{overflow-y:scroll;padding:0 1em 0 0;max-height:70vh}

.ScoreSheet{text-align:center;width:1100px;margin:0 auto;position:absolute;top:1.5em;left:50%;top:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}.ScoreSheet h1,.ScoreSheet h2{color:#fff;-webkit-text-stroke:1px black}.ScoreSheet i{font-size:2em}.ScoreSheet .table{margin-top:1.5em;border-radius:11px;max-height:70vh}.ScoreSheet .table ul:not(.data-div){display:grid;grid-template-columns:1.6fr 1fr 1fr 1fr 1fr 1fr;grid-gap:.25em;gap:.25em;margin-bottom:.25em;text-transform:uppercase}.ScoreSheet .table .data-div{height:47vh;overflow-y:scroll;padding:0 .5em 0 0;width:calc(100% + .5em + 20px)}.ScoreSheet .table li{background:var(--yellow);border-radius:5px;display:flex;justify-content:center;align-items:center;width:100%;height:100%;padding:1.5em 0}.ScoreSheet .table .top-div li{background:#d1a81f}.ScoreSheet .table .top-div .score{grid-column:3/5}.ScoreSheet .table .top-div .win-lose{grid-column:5/6;grid-row:1/3}.ScoreSheet .table .top-div .detail{grid-column:6/7;grid-row:1/3}.detail-frame{text-align:center;background:var(--red);border-radius:15px;width:1100px;margin:0 auto;line-height:22px;padding:2em;position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}.detail-frame h1{margin-bottom:.5em}.detail-frame section{display:flex;justify-content:space-around}.detail-frame section .members{display:flex;flex-direction:column;width:47%;border-radius:11px}.detail-frame section .members .member{background:#fff;border-radius:11px;display:flex;margin-bottom:.5em;padding:.5em;align-items:center}.detail-frame section .members h1{font-size:20px}.detail-frame .index-btn{font-size:30px;padding:.5em;margin:0 auto}.detail-frame .fa-trophy{font-size:5em;color:gold;position:absolute;left:1.5em;top:0;-webkit-transform:rotate(320deg);transform:rotate(320deg)}.fa-trophy{color:var(--red)}.fa-tired{color:green}

